    width: 12%;
    height: 47%;
    bottom: 80%;
    left: 43%;
    background-size: contain;
    background-position: left top;
    display: inline-block;
    position: absolute;
    background-repeat: no-repeat;
    @include respond-to(xs) {
      display: none;
    }
  }
  &:after {
    content: '';
    background-image: url(../images/svg/Path-2.svg);
    width: 10%;
    height: 39%;
    background-size: contain;
    background-position: left top;
    top: 85%;
    right: 50%;
    display: inline-block;
    position: absolute;
    background-repeat: no-repeat;
    @include respond-to(xs) {
      display: none;
    }
  }
}



